@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

/* ENTIRE WEBPAPP */
.App {
  margin: 0% 10%;
  padding: 15vh 50px 100vh 50px;
  background-color: rgba(228, 228, 228, 0.74);
  font: 18px;
