  font-style: conde;
  z-index: -1;
  font-family: 'Roboto', sans-serif;  
color: rgb(59, 59, 59);
  font-weight: 500;
}

html {
  /* The image used */
  background-image: url("https://images.pexels.com/photos/298018/pexels-photo-298018.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");

  /* Full height */
  height: 100%;

  /* Create the parallax scrolling effect */
