/* GROUP CARD */
.groups-cards {
  
}

.group-bar img{
  height: 1em;
  width: 60%;
  padding: 3px;
  position: relative;
  box-shadow: 0 0 30px rgb(35, 160, 167);
  border-radius: 10px;
  border: 3px solid rgb(109, 134, 163);
  -webkit-animation: glowing 1s ease-in-out infinite alternate;
  -moz-animation: glowing 1s ease-in-out infinite alternate;
  animation: glowing 1s ease-in-out infinite alternate;
}
@-webkit-keyframes glowing {
  from {
    box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px rgb(111, 0, 255), 0 0 40px rgb(0, 183, 255), 0 0 50px #007fe6, 0 0 60px #007fe6, 0 0 70px #007fe6;
  }
  to {
    box-shadow: 0 0 20px #fff, 0 0 30px #6b2cfc, 0 0 40px #3e14fc, 0 0 50px #4d59ff, 0 0 60px #5f4dff, 0 0 70px #4da9ff, 0 0 80px #4da0ff;
  }
}
.group-bar img:hover {
    color: rgb(18, 19, 43);
    background:  linear-gradient(144deg,#AF40FF, #5B42F3 50%,#00dbeb75);
    box-shadow: 0 0 30px rgb(13, 65, 66);
    transition: .75s ease-in;
    transition: .75s ease-out;
    border: 3px solid rgb(31, 0, 82);
    -webkit-animation: glowing 1s ease-in-out infinite alternate;
    -moz-animation: glowing 1s ease-in-out infinite alternate;
    animation: glowing 1s ease-in-out infinite alternate;
}

